@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', -apple-system, 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background 0.3s ease;
  position: relative;
  overflow: hidden;
}

/* Ìó¨Î°úÌÇ§Ìã∞ Î∞∞Í≤Ω - Î¨ºÎ∞©Ïö∏ Î¨¥Îä¨ */
.App.dark-mode {
  background: 
    radial-gradient(circle at 20% 30%, rgba(255, 107, 157, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 107, 157, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(255, 107, 157, 0.1) 0%, transparent 50%),
    linear-gradient(135deg, #ff6b9d 0%, #ff4d8a 100%);
}

.App.light-mode {
  background: 
    radial-gradient(circle at 20% 30%, rgba(255, 255, 255, 0.5) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 255, 255, 0.5) 0%, transparent 50%),
    linear-gradient(135deg, #fff0f6 0%, #ffe0ec 100%);
}

/* Î¨ºÎ∞©Ïö∏ Î¨¥Îä¨ Îç∞ÏΩîÎ†àÏù¥ÏÖò */
.App::before,
.App::after {
  content: '‚Ä¢';
  position: absolute;
  font-size: 200px;
  color: rgba(255, 255, 255, 0.1);
  z-index: 0;
}

.App::before {
  top: -50px;
  left: -50px;
}

.App::after {
  bottom: -50px;
  right: -50px;
}

.App.timer-active.dark-mode {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
}

.App.timer-active.light-mode {
  background: linear-gradient(135deg, #fff5f8 0%, #ffe5f1 100%);
}

.container {
  max-width: 800px;
  width: 100%;
  position: relative;
  z-index: 1;
}

/* Ìó¨Î°úÌÇ§Ìã∞ Ìó§Îçî - Î¶¨Î≥∏ Ïû•Ïãù */
.header {
  text-align: center;
  color: white;
  margin-bottom: 30px;
  position: relative;
}

.header::before {
  content: 'üéÄ';
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 3em;
  animation: ribbonFloat 3s ease-in-out infinite;
}

@keyframes ribbonFloat {
  0%, 100% { transform: translateX(-50%) translateY(0px) rotate(0deg); }
  50% { transform: translateX(-50%) translateY(-10px) rotate(5deg); }
}

.light-mode .header {
  color: #ff6b9d;
}

.header h1 {
  font-size: 3.5em;
  margin-bottom: 10px;
  font-weight: 900;
  letter-spacing: -2px;
  text-shadow: 
    4px 4px 0px rgba(0, 0, 0, 0.1),
    6px 6px 0px rgba(255, 192, 203, 0.3);
  position: relative;
}

.light-mode .header h1 {
  text-shadow: 
    3px 3px 0px rgba(255, 107, 157, 0.2),
    5px 5px 0px rgba(255, 192, 203, 0.1);
}

.subtitle {
  font-size: 1.3em;
  opacity: 0.95;
  font-weight: 700;
}

.header-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 25px;
}

.icon-btn {
  background: white;
  border: 4px solid #ff6b9d;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  font-size: 1.8em;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 
    0 8px 0px #ff6b9d,
    0 12px 25px rgba(255, 107, 157, 0.4);
  position: relative;
}

.icon-btn::after {
  content: '';
  position: absolute;
  top: -8px;
  right: -8px;
  width: 20px;
  height: 20px;
  background: #ffe0ec;
  border: 3px solid #ff6b9d;
  border-radius: 50%;
  z-index: -1;
}

.icon-btn:hover {
  transform: translateY(-3px) rotate(-5deg) scale(1.1);
  box-shadow: 
    0 11px 0px #ff6b9d,
    0 15px 30px rgba(255, 107, 157, 0.5);
}

.icon-btn:active {
  transform: translateY(3px);
  box-shadow: 
    0 5px 0px #ff6b9d,
    0 8px 20px rgba(255, 107, 157, 0.4);
}

/* Î¶¨Î≥∏ Î∞∞ÎÑà */
.dday-banner {
  background: white;
  border: 4px solid #ff6b9d;
  border-radius: 30px;
  padding: 20px 30px;
  text-align: center;
  color: #ff6b9d;
  font-size: 1.5em;
  margin-bottom: 20px;
  box-shadow: 
    0 8px 0px #ff6b9d,
    0 12px 30px rgba(255, 107, 157, 0.3);
  font-weight: 900;
  position: relative;
  overflow: visible;
}

.dday-banner::before,
.dday-banner::after {
  content: 'üéÄ';
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(-15deg);
  font-size: 2em;
}

.dday-banner::before {
  left: -15px;
}

.dday-banner::after {
  right: -15px;
  transform: translateY(-50%) rotate(15deg);
}

/* ÌïòÌä∏ Ïä§Ìä∏Î¶≠ Î∞∞ÎÑà */
.streak-banner {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  border: 4px solid white;
  border-radius: 30px;
  padding: 20px 30px;
  text-align: center;
  color: white;
  font-size: 1.5em;
  margin-bottom: 20px;
  box-shadow: 
    0 8px 0px rgba(255, 107, 157, 0.5),
    0 12px 30px rgba(255, 107, 157, 0.4);
  font-weight: 900;
  position: relative;
}

.streak-banner::before {
  content: 'üíó';
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.5em;
  animation: heartBeat 1s infinite;
}

@keyframes heartBeat {
  0%, 100% { transform: translateY(-50%) scale(1); }
  50% { transform: translateY(-50%) scale(1.2); }
}

/* ÌÇ§Ìã∞ Ï†ïÎ≥¥ Ïπ¥Îìú */
.quick-info {
  display: flex;
  gap: 15px;
  margin-bottom: 25px;
}

.quick-info-item {
  flex: 1;
  background: white;
  border: 4px solid #ff6b9d;
  border-radius: 25px;
  padding: 18px;
  text-align: center;
  color: #ff6b9d;
  font-size: 1em;
  box-shadow: 
    0 6px 0px #ff6b9d,
    0 10px 25px rgba(255, 107, 157, 0.3);
  font-weight: 800;
  position: relative;
}

.quick-info-item::before {
  content: '‚ú®';
  position: absolute;
  top: -15px;
  right: 10px;
  font-size: 1.5em;
}

.quick-info-item span {
  display: block;
  opacity: 0.8;
  font-size: 0.9em;
  margin-bottom: 5px;
}

.quick-info-item strong {
  display: block;
  font-size: 1.4em;
}

/* Î¶¨Î≥∏ Î™®ÏùòÍ≥†ÏÇ¨ Î≤ÑÌäº */
.mock-exam-btn {
  width: 100%;
  padding: 25px;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  color: white;
  border: 5px solid white;
  border-radius: 35px;
  font-size: 1.6em;
  font-weight: 900;
  cursor: pointer;
  margin-bottom: 30px;
  transition: all 0.3s ease;
  box-shadow: 
    0 10px 0px rgba(255, 107, 157, 0.7),
    0 15px 35px rgba(255, 107, 157, 0.5);
  position: relative;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.mock-exam-btn::before {
  content: 'üéÄ';
  position: absolute;
  left: 25px;
  top: 50%;
  transform: translateY(-50%) rotate(-15deg);
  font-size: 1.3em;
}

.mock-exam-btn::after {
  content: 'üéÄ';
  position: absolute;
  right: 25px;
  top: 50%;
  transform: translateY(-50%) rotate(15deg);
  font-size: 1.3em;
}

.mock-exam-btn:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 
    0 15px 0px rgba(255, 107, 157, 0.7),
    0 20px 40px rgba(255, 107, 157, 0.6);
}

.mock-exam-btn:active {
  transform: translateY(5px);
  box-shadow: 
    0 5px 0px rgba(255, 107, 157, 0.7),
    0 10px 25px rgba(255, 107, 157, 0.5);
}

/* ÌÇ§Ìã∞ Í≥ºÎ™© Ïπ¥Îìú - Î¶¨Î≥∏ Ïû•Ïãù */
.subject-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.subject-card {
  background: white;
  border: 5px solid;
  border-radius: 30px;
  padding: 30px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 
    0 8px 0px currentColor,
    0 12px 30px rgba(0, 0, 0, 0.15);
  position: relative;
  overflow: visible;
}

.subject-card::before {
  content: 'üéÄ';
  position: absolute;
  top: -15px;
  right: -10px;
  font-size: 2em;
  transform: rotate(25deg);
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.2));
}

.subject-card:hover {
  transform: translateY(-12px) rotate(-2deg);
  box-shadow: 
    0 12px 0px currentColor,
    0 18px 40px rgba(0, 0, 0, 0.2);
}

.subject-card:hover::before {
  animation: ribbonBounce 0.5s ease infinite;
}

@keyframes ribbonBounce {
  0%, 100% { transform: rotate(25deg) scale(1); }
  50% { transform: rotate(25deg) scale(1.2); }
}

.subject-card:active {
  transform: translateY(4px) scale(0.98);
  box-shadow: 
    0 4px 0px currentColor,
    0 8px 20px rgba(0, 0, 0, 0.15);
}

.subject-emoji {
  font-size: 4em;
  margin-bottom: 15px;
  filter: drop-shadow(3px 3px 6px rgba(0, 0, 0, 0.1));
  animation: emojiFloat 3s ease-in-out infinite;
}

@keyframes emojiFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-8px); }
}

.subject-name {
  font-size: 1.5em;
  font-weight: 900;
  color: #333;
  margin-bottom: 8px;
  letter-spacing: -0.5px;
}

.subject-time {
  font-size: 1.2em;
  font-weight: 800;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Î¶¨Î≥∏ Ï†ïÎ≥¥ Î∞ïÏä§ */
.info-box {
  background: white;
  border: 4px solid #ff6b9d;
  border-radius: 25px;
  padding: 25px 30px;
  color: #ff6b9d;
  text-align: center;
  box-shadow: 
    0 6px 0px #ff6b9d,
    0 10px 30px rgba(255, 107, 157, 0.3);
  position: relative;
}

.info-box::before {
  content: 'üíó';
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.5em;
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.1));
}

.info-box p {
  margin: 10px 0;
  font-size: 1.1em;
  font-weight: 700;
  line-height: 1.6;
}

/* ÌÇ§Ìã∞ Î™®Îã¨ */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 107, 157, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  backdrop-filter: blur(15px);
}

.modal-content {
  background: white;
  border: 6px solid #ff6b9d;
  border-radius: 40px;
  padding: 45px;
  max-width: 500px;
  width: 100%;
  box-shadow: 
    0 12px 0px #ff6b9d,
    0 18px 50px rgba(255, 107, 157, 0.5);
  animation: modalPop 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  position: relative;
}

@keyframes modalPop {
  0% { transform: scale(0) rotate(-10deg); opacity: 0; }
  100% { transform: scale(1) rotate(0deg); opacity: 1; }
}

.modal-content::before {
  content: 'üéÄ';
  position: absolute;
  top: -25px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 3em;
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.2));
}

.modal-content h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #ff6b9d;
  font-size: 2.5em;
  font-weight: 900;
  text-shadow: 2px 2px 4px rgba(255, 107, 157, 0.2);
}

.modal-content p {
  text-align: center;
  color: #666;
  margin-bottom: 30px;
  font-size: 1.2em;
  font-weight: 700;
}

.modal-desc {
  font-size: 1em !important;
  color: #999 !important;
}

/* ÌïòÌä∏ Ìú¥Ïãù Î≤ÑÌäº */
.break-options {
  display: flex;
  gap: 12px;
  margin-bottom: 20px;
}

.break-options button {
  flex: 1;
  padding: 20px;
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  color: white;
  border: 4px solid white;
  border-radius: 20px;
  font-size: 1.1em;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 
    0 6px 0px rgba(255, 107, 157, 0.6),
    0 10px 25px rgba(255, 107, 157, 0.4);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.break-options button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 
    0 9px 0px rgba(255, 107, 157, 0.6),
    0 13px 30px rgba(255, 107, 157, 0.5);
}

.break-options button:active {
  transform: translateY(3px);
  box-shadow: 
    0 3px 0px rgba(255, 107, 157, 0.6),
    0 7px 20px rgba(255, 107, 157, 0.4);
}

.skip-btn {
  width: 100%;
  padding: 18px;
  background: white;
  color: #ff6b9d;
  border: 4px solid #ff6b9d;
  border-radius: 20px;
  font-size: 1.1em;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 0px #ff6b9d;
}

.skip-btn:hover {
  background: #fff5f8;
  transform: translateY(-2px);
  box-shadow: 0 8px 0px #ff6b9d;
}

/* Î™®ÏùòÍ≥†ÏÇ¨ Ïä§ÏºÄÏ§Ñ */
.mock-exam-modal {
