/* Ï§ÄÎπÑ ÌôîÎ©¥ - Î∞òÏßùÏù¥Îäî ÌÇ§Ìã∞ */
.ready-container {
  max-width: 600px;
  width: 100%;
  text-align: center;
  color: white;
}

.light-mode .ready-container {
  color: #ff6b9d;
}

.ready-subject {
  font-size: 4em;
  font-weight: 900;
  margin-bottom: 35px;
  text-shadow: 
    4px 4px 0px rgba(0, 0, 0, 0.1),
    6px 6px 0px rgba(255, 192, 203, 0.3);
  animation: kittySparkle 2s infinite;
}

@keyframes kittySparkle {
  0%, 100% { 
    transform: scale(1) rotate(0deg);
    filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  }
  50% { 
    transform: scale(1.05) rotate(2deg);
    filter: drop-shadow(0 0 30px rgba(255, 255, 255, 0.8));
  }
}

.ready-tip {
  background: white;
  border: 5px solid rgba(255, 255, 255, 0.5);
  border-radius: 30px;
  padding: 35px;
  font-size: 1.5em;
  line-height: 1.8;
  margin-bottom: 45px;
  box-shadow: 
    0 8px 0px rgba(255, 255, 255, 0.3),
    0 12px 35px rgba(255, 107, 157, 0.3);
  font-weight: 800;
  color: #ff6b9d;
  position: relative;
}

.ready-tip::before {
  content: 'üíó';
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.5em;
}

.ready-countdown {
  font-size: 10em;
  font-weight: 900;
  margin-bottom: 25px;
  animation: kittyCountdown 1s infinite;
  text-shadow: 
    5px 5px 0px rgba(0, 0, 0, 0.1),
    8px 8px 0px rgba(255, 192, 203, 0.3);
}

@keyframes kittyCountdown {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2) rotate(-5deg); }
}

.ready-message {
  font-size: 1.8em;
  font-weight: 900;
}

/* ÌÉÄÏù¥Î®∏ ÌôîÎ©¥ - ÌÇ§Ìã∞ LCD */
.timer-container {
  max-width: 600px;
  width: 100%;
  position: relative;
}

.mock-exam-progress {
  text-align: center;
  color: white;
  font-size: 1.4em;
  margin-bottom: 25px;
  padding: 18px;
  background: white;
  border: 5px solid rgba(255, 255, 255, 0.5);
  border-radius: 25px;
  font-weight: 900;
  color: #ff6b9d;
  box-shadow: 
    0 6px 0px rgba(255, 255, 255, 0.3),
    0 10px 25px rgba(255, 107, 157, 0.3);
}

.timer-header {
  text-align: center;
  margin-bottom: 45px;
}

.timer-header h2 {
  color: white;
  font-size: 3.5em;
  font-weight: 900;
  text-shadow: 
    4px 4px 0px rgba(0, 0, 0, 0.1),
    6px 6px 0px rgba(255, 192, 203, 0.3);
}

.light-mode .timer-header h2 {
  color: #ff6b9d;
  text-shadow: 
    3px 3px 0px rgba(255, 107, 157, 0.2),
    5px 5px 0px rgba(255, 192, 203, 0.1);
}

.timer-display {
  background: white;
  border: 8px solid #ff6b9d;
  border-radius: 40px;
  padding: 70px 45px;
  text-align: center;
  margin-bottom: 35px;
  box-shadow: 
    0 12px 0px #ff6b9d,
    0 18px 50px rgba(255, 107, 157, 0.4);
  transition: all 0.3s ease;
  position: relative;
}

.timer-display::before {
  content: 'üéÄ';
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%) rotate(-15deg);
  font-size: 3em;
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.2));
}

.timer-display.break-mode {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
  border-color: white;
}

.timer-display.warning {
  background: linear-gradient(135deg, #fff9e6 0%, #ffe9b3 100%);
  animation: kittyWarning 1s infinite;
}

.timer-display.critical {
  background: linear-gradient(135deg, #ffe6e6 0%, #ffb3b3 100%);
  animation: kittyCritical 0.5s infinite;
}

@keyframes kittyWarning {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 
      0 12px 0px #ffd93d,
      0 18px 50px rgba(255, 217, 61, 0.5);
  }
  50% { 
    transform: scale(1.03);
    box-shadow: 
      0 15px 0px #ffd93d,
      0 22px 60px rgba(255, 217, 61, 0.7);
  }
}

@keyframes kittyCritical {
  0%, 100% { 
