/* ÌïòÌä∏ ÌÜ†Í∏Ä */
.toggle {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 38px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ffc0cb;
  transition: .3s;
  border-radius: 38px;
  border: 4px solid #ff6b9d;
}

.slider:before {
  position: absolute;
  content: "üíó";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 2px;
  background-color: white;
  transition: .3s;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  border: 2px solid #ff6b9d;
}

input:checked + .slider {
  background: linear-gradient(135deg, #ff6b9d 0%, #ff8fab 100%);
}

input:checked + .slider:before {
  transform: translateX(32px);
}

.date-input, .number-input {
  padding: 12px 20px;
  border: 4px solid #ffc0cb;
  border-radius: 15px;
  font-size: 1.1em;
